import React, { useState, useEffect } from "react";
import { getAccount } from "../../services/http";
import _ from "lodash";
import Nav from "../common/nav";
import Footer from "../common/footer";
import Table from "../common/table";

const SummaryExplore = ({ account }) => {
  const [transactions, setTransactions] = useState(null);
  const [summary, setSummary] = useState(null);

  useEffect(() => {
    getAccountData();
  }, []);

  const getAccountData = async () => {
    const { data } = await getAccount({ account: account });
    console.log(data);
    setSummaryData(data.summary);
    setTransactions(data.transactions);
  };

  const setSummaryData = (data) => {
    setSummary(
      Object.entries(data).map((v, i) => (
        <div>
          <h3>{v[0]}</h3>
          <h3>${v[1]}</h3>
        </div>
      ))
    );
  };

  return (
    <>
      <Nav />
      <section>
        <div className="container">
          <div className="flex-wrapper mobile-apart">
            <h1>{account}</h1>
          </div>
          <hr />
          <div className="flex-wrapper mb-5">{summary && summary}</div>
          <div>
            {!_.isEmpty(transactions) && (
              <div>
                <h5>Transactions</h5>
                <Table props={transactions} />
              </div>
            )}
          </div>
        </div>
      </section>
      <Footer />
    </>
  );
};
export default SummaryExplore;
